import { Injectable, OnInit, OnDestroy } from '@angular/core';
import { UtilsService } from './utils.service';
import { environment } from '../../environments/environment';
import _ from 'lodash';

const STORE_KEY = 'NDO6-USER-SETTINGS';
const DEFAULT_SERVER_HOST = 'http://localhost:6001';

@Injectable()
export class UserSettingsService implements OnInit, OnDestroy {
  constructor(private u: UtilsService) {
    const sts = this.u.storage.get(STORE_KEY) || {};
    _.extend(this.settings, sts);
    _.defaults(this.settings, {
      host: DEFAULT_SERVER_HOST,
      debug: false,
      delay: 1000,
      nick: 'io',
      map: '',
      token: ''
    });
  }
  settings: any = {};
  private keep(value, target) {
    if (value) {
      this.settings[target] = value;
    }
  }
  update() {
    this.u.storage.set(STORE_KEY, this.settings);
  }
  getUrl(...parts: any[]) {
    const self = this;
    const args = Array.prototype.slice.call(arguments);
    const host = environment.production ? self.u.getOrigin() : (self.settings['host'] || DEFAULT_SERVER_HOST);
    args.unshift(host);
    return self.u.checkUrl.apply(null, args);
  }
  logdata(token = '', data: any = null) {
    this.settings.token = token;
    this.keep((data || {}).name, 'map');
    this.keep((data || {}).nick, 'nick');
    this.update();
  }
  ngOnInit() {
  }
  ngOnDestroy() {
    this.update();
  }
}



