import { Injectable } from '@angular/core';
import {UtilsService} from './utils.service';
import _ from 'lodash';

const LOGTYPES = {
  error: 'error',
  warning: 'warning',
  info: 'info'
};

@Injectable()
export class LogService {

  constructor(private u: UtilsService) { }
  lines = [];
  private add(txt, type = LOGTYPES.info) {
    const now = new Date();
    const line = {
      time: now.getTime(),
      type: type,
      text: txt
    };
    this.lines.push(line);
    console.log(line);
  }
  error(err, show = false) {
    this.add(this.u.getErrorMessage(err), LOGTYPES.error);
    console.error(err);
    if (show) { this.u.error(err); }
  }
  warning(txt) {
    this.add(txt, LOGTYPES.warning);
    console.warn(txt);
  }
  info(txt, obj = null) {
    if (_.isObject(obj)) {
      console.log(txt, obj);
      txt = txt + ' ' + JSON.stringify(obj);
    }
    this.add(txt);
  }
}



