import { Component, NgZone, OnInit } from '@angular/core';
import { environment } from '../environments/environment';
import { fadeAnimation } from './app.animations';
import {InteractionService} from './services/interaction.service';
import {UserSettingsService} from './services/user-settings.service';
import {UtilsService} from './services/utils.service';
import {MapsService} from './services/maps.service';
import {Ndo6Service} from './services/ndo6.service';
import {LogService} from './services/log.service';
import _ from 'lodash';


const GOOGLE_MAP_URL = 'https://maps.googleapis.com/maps/api/js?key={URL}&libraries=geometry,places&callback={CALLBACK}';
const GOOGLE_CALLBACK_NAME = 'googleMapsInitialized';
const initializer = {
  run: (info) => {
    return new Promise((resolve) => {
      window[GOOGLE_CALLBACK_NAME] = resolve;
      let url = GOOGLE_MAP_URL.replace(/\{URL\}/g, info.googleKey);
      url = url.replace(/\{CALLBACK\}/g, GOOGLE_CALLBACK_NAME);
      // console.log('URL: %s', url);
      const script = document.createElement('script');
      script.src = url;
      document.body.appendChild(script);
    });
  }
};

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  animations: [fadeAnimation]
})
export class AppComponent implements OnInit {
  constructor(private interaction: InteractionService,
              private log: LogService,
              private user: UserSettingsService,
              public u: UtilsService,
              private maps: MapsService,
              public ndo6: Ndo6Service,
              private zone: NgZone) {
    this.u.idle = true;
  }
  initialized = false;
  info: any = {};
  debug = !environment.production;
  refresh() {
    const self = this;
    self.u.err = null;
    self.info = {};
    self.ndo6.reset();
    self.interaction.getInfo().subscribe((data: any) => {
      self.info = data;
      if (data.debug === true) { self.debug = true; }
      initializer.run(data).then(() => {
        self.maps.create((ctx) => {
          self.ndo6.clickOnMarker = (m) => {
            self.clickOnMarker(m);
          };
          self.ndo6.activate(ctx);
        });
        self.u.idle = false;
      }, err => {
        self.log.error(err, true);
        self.u.idle = false;
      });
    }, err => {
      self.log.error(err, true);
      self.u.idle = false;
    });
  }
  ngOnInit() {
    const self = this;
    self.ndo6.events.subscribe((e) => {
      self.zone.run(() => {
        switch (e.type) {
          case 'marker':
            self.clickOnMarker(e.data);
            break;
          case 'firstpos':
            self.initialized = !!self.ndo6.last || !!self.ndo6.followMarker;
            break;
          case 'changepos':
            if (!!self.user.settings.token) {
              self.log.info('SEND POSITION - event: ', e);
              self.interaction.position({
                latitude: e.data.latitude,
                longitude: e.data.longitude,
                timestamp: (new Date()).getTime()
              }, (err) => {
                if (err) {
                  self.log.error(err);
                  self.user.logdata();
                } else {
                  self.log.info('Position sending successfully');
                }
              });
            }
            break;
        }
      });
    });
    self.initialized = !!self.ndo6.last || !!self.ndo6.followMarker;
    this.refresh();
  }
  overpage(type, o = null) {
    this.u.overpage.options = o;
    this.u.overpage.type = type;
  }
  hideError() {
    this.u.err = null;
  }
  clickOnMarker(m) {
    const self = this;
    self.overpage('marker', {
      marker: m,
      nick: self.ndo6.getNick(m),
      latitude: m.position.lat(),
      longitude: m.position.lng()
    });
  }
  location() {
    if (this.ndo6.last) {
      this.ndo6.centerMap(this.ndo6.last.marker.position);
    }
  }
  closeOverpage() {
    this.u.closeOverpage();
  }
}




