import { Injectable, EventEmitter } from '@angular/core';
import {MapsService} from './maps.service';
import {UserSettingsService} from './user-settings.service';
import {UtilsService} from './utils.service';
import {LogService} from './log.service';
import _ from 'lodash';
declare const google: any;

export class Ndo6Event {
  type: string;
  data: any;
  constructor(type: string, data: any = null) {
    this.type = type;
    this.data = data;
  }
}

export class Position {
  id: string;
  title: string;
  label: string;
  type: string;
  last = false;
  latitude: any = null;
  longitude: any = null;
  altitude: any = null;
  accuracy: any = null;
  altitudeAccuracy: any = null;
  heading: any = null;
  speed: any = null;
  timestamp: any = null;
  marker: any = null;
  constructor(public info: any = null, public data: any = null) {
    this.keep(info);
    this.id = data ? data.id : '';
    this.title = data ? data.title : '';
    this.label = data ? data.label : '';
    this.type = data ? data.type : '';
  }
  isValid() {
    return (this.latitude && this.longitude);
  }
  getLatLng() {
    return new google.maps.LatLng(this.latitude, this.longitude);
  }
  sameOf(pos) {
    return pos &&
      pos.latitude === this.latitude &&
      pos.longitude === this.longitude &&
      pos.altitude === this.altitude;
  }
  keep(pos) {
    if (pos) {
      this.id = pos.id;
      this.title = pos.title;
      this.label = pos.label;
      this.type = pos.type;
      if (pos.timestamp) { this.timestamp = pos.timestamp; }
      if (pos.coords) { pos = pos.coords; }
      this.latitude = pos.latitude;
      this.longitude = pos.longitude;
      this.altitude = pos.altitude;
      this.accuracy = pos.accuracy;
      this.altitudeAccuracy = pos.altitudeAccuracy;
      this.heading = pos.heading;
      this.speed = pos.speed;
    }
  }
  dispose() {
    if (this.marker) {
      this.marker.setMap(null);
    }
  }
}

const GEOLOCATION_OPTIONS = {
  maximumAge: 10,
  enableHighAccuracy: false,
  timeout: 10000
};


@Injectable()
export class Ndo6Service {
  events: EventEmitter<Ndo6Event> = new EventEmitter();
  constructor(private maps: MapsService,
              private user: UserSettingsService,
              private u: UtilsService,
              private log: LogService) { }
  private positionChecker = null;
  last: Position = null;
  session: any = {
    user: {},
    map: null,
    context: null
  };
  onfirstpos = false;
  followMarker = null;
  watchId = null;
  active = false;
  center = false;
  posErrorCounter = 0;
  clickOnMarker = null;
  setZoom(zoom) {
    const self = this;
    if (!zoom || !self.session.context) { return; }
    const listener = google.maps.event.addListener(self.session.context.map, 'idle', () => {
      self.session.context.map.setZoom(zoom);
      google.maps.event.removeListener(listener);
    });
  }
  /**
   * centra la mappa
   */
  centerMap(pos, zoom = null, finder = null) {
    const self = this;
    if (!self.session.context || !pos) { return; }
    // il centro è considerato più in alto per
    // lasciare lo spazio al monitor
    const bounds = self.session.context.map.getBounds();
    if (!bounds) { return; }

    // Calcola le coordinate del centro
    const gpos = self.maps.getLatLng(pos);

    // Imposta il centro della mappa
    self.session.context.map.setCenter(gpos);

    const mrk = finder ? finder() : null;
    if (mrk) {
      // Se ha trovato il marker lo anima
      mrk.setAnimation(google.maps.Animation.BOUNCE);
      setTimeout(function () {
        mrk.setAnimation(null);
      }, 1000);
    }
    self.setZoom(zoom);
  }
  watchGeo(cb) {
    const self = this;
    self.watchId = navigator.geolocation.watchPosition(cb, function (err: any) {
      switch (err.code) {
        case 1: self.log.error('Geolocation permission denied'); break;
        case 2: self.log.error('Geolocation position unavailable'); break;
        case 3: self.log.warning('Geolocation timed out'); break;
        default: self.log.error(err);
      }
    }, GEOLOCATION_OPTIONS);
  }
  checkGeo() {
    const self = this;
    return new Promise((resolve, reject) => {
      if (!navigator.geolocation) {
        const err = 'Geolocation is not supported by this browser.';
        self.log.error(err);
        return reject(err);
      } else {
        navigator.geolocation.getCurrentPosition(resolve, function (err: any) {
          switch (err.code) {
            case 1: self.log.error('Geolocation permission denied'); break;
            case 2: self.log.error('Geolocation position unavailable'); break;
            case 3: self.log.warning('Geolocation timed out'); break;
            default: self.log.error(err);
          }
          reject(err);
        }, GEOLOCATION_OPTIONS);
      }
    });
  }
  reset() {
    if (this.positionChecker) {
      clearTimeout(this.positionChecker);
    }
    this.active = false;
    this.clickOnMarker = null;
  }
  samePos(p1, p2) {
    return p1 && p2 && p1 === p2;
  }
  setPos(pos) {
    const self = this;
    const nick = (self.session.user.nick || 'self');
    const npos = new Position(pos, {
      id: 'user@' + nick,
      title: nick,
      label: nick.slice(0, 1),
      type: 'user'
    });
    if (!self.last) {
      self.last = new Position();
      self.onfirstpos = true;
    }
    if (!self.last.marker) {
      self.last.marker = self.getMarker(self.last);
    }
    self.log.info('POSITION: ', npos);
    if (!self.last.sameOf(npos) && npos.isValid()) {
      const latlng = npos.getLatLng();
      self.last.marker.setPosition(latlng);
      self.log.info('EMIT POSITION: ', npos);
      self.events.emit(new Ndo6Event('changepos', {
        latitude: npos.latitude,
        longitude: npos.longitude
      }));
      const center = self.center || self.samePos(self.followMarker, self.last.marker);
      if (center === true) {
        self.centerMap(npos);
        self.center = false;
      }
      if (self.onfirstpos) {
        self.events.emit(new Ndo6Event('firstpos'));
        self.onfirstpos = false;
      }
    }
  }
  checkPos() {
    const self = this;
    self.positionChecker = setTimeout(() => {
      self.checkGeo()
        .then((pos: any) => {
          self.posErrorCounter = 0;
          self.log.info('read position: ' + self.u.format('{latitude},{longitude}', pos.coords));
          self.setPos(pos);
          if (self.active) {
            self.checkPos();
          }
        }, err => {
          if (err) {
            self.posErrorCounter++;
          }
          self.checkPos();
        });
    }, self.user.settings.delay || 1000);
  }
  clearGeo() {
    const self = this;
    if (self.positionChecker) {
      clearTimeout(self.positionChecker);
    }
  }
  stopGeo() {
    const self = this;
    if (self.watchId) {
      navigator.geolocation.clearWatch(self.watchId);
    }
  }
  activateW(context) {
    const self = this;
    self.session.context = context;
    self.stopGeo();
    self.center = true;
    self.watchGeo(function(pos){
      self.setPos(pos);
    });
    self.active = true;
  }
  activate(context) {
    const self = this;
    self.session.context = context;
    self.clearGeo();
    self.center = true;
    self.checkPos();
    self.active = true;
  }
  getMarkerIcon(url) {
    if (url) {
      return {
        url: url,
        size: new google.maps.Size(22, 40),
        origin: new google.maps.Point(0, 0),
        anchor: new google.maps.Point(11, 40)
      };
    }
  }
  getMarker(info) {
    const self = this;
    if (!self.session.context) { return null; }
    const latlnt = self.maps.getLatLng(info);
    const m = new google.maps.Marker({
      map: self.session.context.map,
      label: info.label || 'P',
      position: latlnt,
      title: info.title || info.description,
      icon: self.getMarkerIcon(info.icon)
    });
    m.ndo6 = {
      id: self.u.guid()
    };
    _.extend(m.ndo6, info);
    google.maps.event.addListener(m, 'click', function() {
      self.events.emit(new Ndo6Event('marker', m));
    });
    return m;
  }
  getNick(m) {
    if (this.last && m === this.last.marker) {
      return this.user.settings.nick;
    }
    return m.nick;
  }
  getLastPos() {
    if (this.last && this.last.isValid()) {
      return {
        latitude: this.last.latitude,
        longitude: this.last.longitude,
        timestamp: this.last.timestamp
      };
    }
  }
}



